@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@100;300;400;700&display=swap);
* {
    margin: 0;
    font-family: 'Roboto', sans-serif;
    font-weight: 300;
    text-decoration: none;
    color: inherit;
}

body {
    background-color: #f5f5f5;
}

a {
    color: inherit;

}

a:hover {
    color: '#000'
}

.linha {
    height: 1px;
    width: 100%;
    background: #eee;
    margin: 30px 0;
}

/*
    @ Menu
*/
#menu {
    display: flex;
    padding: 10px 30px;
    color: #fff;
    background: #000;
    align-items: center;
}

#menu #titulo {
    display: flex;
    margin-right: 20px;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

#menu #titulo a {
    font-weight: bold;
    font-size: 1.3rem;
}

#menu #titulo .subtitulo {
    font-weight: normal;
    font-size: 0.9rem;
    text-align: center;
}

#menu #lista {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: center;
}

#menu #lista a {
    margin: 5px;
    transition: all 0.5s;
}

#menu #lista .selecionado {
    color: #F8427C;
}

#menu #lista a:hover {
    color: #F8427C;
}

#menu .botao {
    border: solid 1px #F8427C;
    color: #F8427C;
    padding: 5px;
    border-radius: 2px;
}

#menu .botao:hover {
    background: #F8427C;
    color: #fff !important;
    border: solid 1px #F8427C;
    cursor: pointer;
}

/*
    @ Cabecalho
*/

#cabecalho {
    background-color: #9e003d;
    background: url(/pibid-site/static/media/background.a1bd9607.jpg) no-repeat top center;
    background-attachment: fixed;
    background-size: cover;
    height: 50vh;
    min-height: 150px;
    display: flex;
    align-items: center;
    justify-content: center;
}

#cabecalho #conteudo {
    color: #fff;
    text-align: center;
    display: flex;
    align-items: center;
}

#cabecalho img {
    height: 20vh;
    min-height: 120px;
}

/*
    @ Apresentação
*/

#apresentacao {
    padding: 120px 10%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    text-align: center;
}

#apresentacao p {
    font-size: 1.2rem;
}

#apresentacao .descricao {
    font-size: 1.5rem;
    margin-bottom: 20px;
}

#apresentacao .subsubdescricao {
    margin: 5px 0;
}

/*
    @ Sociais
*/

#sociais {
    padding: 150px 0;
    background: url(/pibid-site/static/media/background.a1bd9607.jpg) no-repeat bottom center;
    background-attachment: fixed;
    background-size: cover;
    color: #fff;
    text-align: center;
}

#sociais .link {
    color: #fff;
}

#sociais .link:hover {
    transition: all 0.2s;
    color: #e0e0e0;
}

/*
    @ Galeria
*/

#galeria {
    padding: 50px 0;
    text-align: center;
}

#galeria .item img {
    width: 100%;
    height: 400px;
    object-fit: cover;
}

/*
    @ Projetos
*/

#projeto {
    padding: 50px 0;
}

#projeto h1, h3 {
    text-align: center;
}

#projeto #lista {
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    margin: 0 5%;
}

#projeto .item {
    background-color: #fff;
    width: calc(50% - 10px);
    margin: 5px;
}

@media only screen and (max-width: 600px) {
    #projeto .item {
        width: calc(100%);
    }
  }

#projeto .item h2 {
    margin-bottom: 10px;
}

#projeto .item .descricao {
    padding: 5%;
}

#projeto .item .descricao p {
    text-align: justify;
}

#projeto .item img {
    width: 100%;
    object-fit: cover;
    height: 200px;
}

/*
    @ Rodape
*/

#rodape {
    background-color: #000;
    color: #fff;
    padding: 20px;
    text-align: center;
}
